* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  background-color: black;
}

.nav {
  display: flex;
  background-image: linear-gradient(green, rgb(66, 95, 7));
  justify-content: space-around;
  align-items: center;
  height: 80px;
  color: black;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  position: relative;
  width: 100%;
}

.nav-fixed {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  opacity: 0.9;
}

.logo {
  width: 190px;
  height: 65px;
  transform: scale(1.3);
}

ul {
  display: flex;
  list-style: none;
  margin: 0;
}

.item {
  margin-left: 20px;
  border-left: 1px solid black;
  border-right: 1px solid black;
  padding: 10px;
  font-size: 20px;
  cursor: pointer;
  width: 120px;
  text-align: center;
  position: relative;
  font-family: Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.item:hover {
  opacity: 0.8;
  background-color: chartreuse;
}

.header {
  font-size: 48px;
  margin: 5px 0;
}

h1 {
  text-align: center;
  font-weight: bold;
  letter-spacing: 2px;
  margin: 0;
  text-decoration: underline;
}

.pay-info {
  display: flex;
  justify-content: space-around;
  margin-top: 10px;
}

.products-conteiner {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  row-gap: 50px;
  background-image: linear-gradient(60deg, rgb(12, 11, 11), rgb(56, 54, 54), rgb(66, 65, 65));
  padding: 10px;
}

.product {
  width: 200px;
  height: 280px;
  background: white;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 35px;
  border-radius: 10px;
  position: relative;
  margin-top: 10px;
  margin-left: 25%;
  transition: transform 0.4s ease;
}

.product:hover {
  transform: scale(1.1);
}

.products-image {
  width: 200px;
  height: 100px;
}

.name {
  text-transform: uppercase;
  color: rgba(56, 54, 54, 0.87);
  font-weight: bold;
  position: relative;
}

.description {
  color: rgba(56, 54, 54, 0.87);
  margin: 0;
  position: relative;
}

input,
label {
  cursor: pointer;
}

label {
  width: 120px;
}

button {
  width: 135px;
  height: 30px;
  background-image: radial-gradient(green, rgb(23, 172, 23), rgb(247, 247, 18));
  color: black;
  outline: none;
  position: absolute;
  left: 50%;
  bottom: 5px;
  transform: translateX(-50%);
  font-weight: bold;
  border: 1px solid black;
  box-sizing: content-box;
  position: relative;
  transition: transform .4s ease-in-out;
}

button:hover {
  box-shadow: 0 0 5px 2px green;
  transform: scale(1.05) translateX(-50%);
}

/* Cart*/
.cart-conteiner {
  position: relative;
  left: 300px;
}

.fa-shopping-cart {
  color: white;
  font-size: 28px;
  margin-top: 10px;
  cursor: pointer;
  text-align: center;
  transition: all 0.3s ease;
}

.fa-shopping-cart:hover {
  transform: rotate(30deg);
}

.count-products {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  right: -5px;
  top: -2px;
  text-align: center;
  font-size: 10px;
}

.empty-cart {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.cart-info {
  width: 200px;
  background-color: rgb(221, 215, 215);
  color: black;
  border-radius: 10px;
  position: absolute;
  top: 50px;
  left: -155px;
  margin-top: 10px;
  z-index: 4;
}

.cart-info::before {
  content: "";
  width: 25px;
  height: 50px;
  transform: rotate(45deg) translateX(-100%);
  background-color: rgb(221, 215, 215);
  position: absolute;
  left: 85%;
  top: 5px;
}

.cart-info-inactive {
  display: none;
}

.brand-cart {
  text-align: center;
  opacity: 0.9;
  margin: 0;
  margin-top: 5px;
}

.description-cart {
  margin-top: 15px;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  padding: 5px;
}

.cart {
  width: 200px;
  border-bottom: 2px solid black;
  border-radius: 10px;
  background: rgb(182, 163, 163);
}

.img img {
  height: 100px;
  width: 100%;
}

.price-cart {
  display: flex;
  flex-direction: column;
}

.fa-shopping-cart:hover {
  animation: animate-cart-icon 5s ease infinite;
}

.cancel-order {
  z-index: 1;
  margin-top: 5px;
}

.price {
  color: black;
  font-size: 24px;
}

.quantity-update-buttons {
  border: 1px solid black;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  cursor: pointer;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}

.quantity-update-buttons span {
  position: relative;
}

.quantity-update-buttons:hover {
  background-color: rgb(148, 132, 132);
}

.quantity-update-buttons:active span {
  top: 5px;
}

.more-products {
  margin-top: 5px;
  background-image: radial-gradient(rgb(182, 163, 163), rgb(128, 114, 114));
  padding: 3px;
  text-align: center;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: 1px solid black;
  position: relative;
}

.more-products:hover {
  opacity: 0.8;
  cursor: pointer;
}

.total-price {
  margin-top: 5px;
  font-size: 20px;
}

.total {
  color: red;
  font-size: 22px;
}

/* sort*/

.section {
  border: 2px solid greenyellow;
  margin: 10px;
  position: relative;
}

.sort {
  position: relative;
  left: 20%;
}

.radio-inputs-field {
  display: grid;
  grid-template-rows: repeat(3, 22px);
  grid-template-columns: 90px 20px;
  grid-row-gap: 5px;
}

.radio-set {
  position: relative;
}

.sort-input {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border: 1px solid chartreuse;
  transition: all .3s ease-in;
  -webkit-transition: all .3s ease-in;
  -moz-transition: all .3s ease-in;
  -ms-transition: all .3s ease-in;
  -o-transition: all .3s ease-in;
  outline: none !important;
}

.sort-input:checked {
  background-color: chartreuse;
  border: 2px solid whitesmoke;
}

.sort-input:hover {
  transform: scale(1.3);
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
}

.sort-input:checked+.radio-dot {
  color: black;
}

.radio-dot {
  font-size: 4em;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -59%);
  -webkit-transform: translate(-50%, -59%);
  -moz-transform: translate(-50%, -59%);
  -ms-transform: translate(-50%, -59%);
  -o-transform: translate(-50%, -59%);
  pointer-events: none;
  transition: color .5s ease .2s;
  -webkit-transition: color .5s ease .2s;
  -moz-transition: color .5s ease .2s;
  -ms-transition: color .5s ease .2s;
  -o-transition: color .5s ease .2s;
}

.header-sort {
  margin-top: 10px;
}

/* filters*/
.filters-header {
  margin-bottom: 30px;
}

.filter-field,
.filter-field-search {
  display: flex;
}

.price-filtr {
  position: relative;
  top: 20%;
  left: 20%;
}

.filters {
  display: flex;
  flex-direction: column;
}

.inputs {
  margin: 10px;
  background: transparent;
  color: white;
  font-family: Geneva, Tahoma, sans-serif;
  font-size: 12px;
  border: none;
  border-bottom: 1px solid greenyellow;
  outline: none;
  position: absolute;
  bottom: -10px;
  left: -10px;
}

.search-text {
  position: relative;
  left: 40px;
  height: 30px;
  display: grid;
  grid-template-columns: 160px 50px;
}

.search-input {
  border: none;
  transition: transform .5s ease-in-out;
  -webkit-transition: transform .5s ease-in-out;
  -moz-transition: transform .5s ease-in-out;
  -ms-transition: transform .5s ease-in-out;
  -o-transition: transform .5s ease-in-out;
  transform: scaleX(0);
  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -ms-transform: scaleX(0);
  -o-transform: scaleX(0);
  transform-origin: right;
}

.search-input-active::placeholder,
.label-style {
  color: rgb(180, 255, 31);
}

.search-input-active {
  padding-left: 25px;
  background: transparent;
  outline: none;
  border: 1px solid yellowgreen;
  border-radius: 15px;
  opacity: 1;
  color: white;
  margin-left: 10px;
  transform: scaleX(1);
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -ms-transform: scaleX(1);
  -o-transform: scaleX(1);
}

.search-icon {
  background-color: green;
  width: 30px;
  height: 30px;
  position: relative;
  top: -3px;
  left: -60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(1.3);
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
}

.search-icon:hover {
  animation: search-icon-animate 1s ease-in-out infinite;
  cursor: pointer;
  -webkit-animation: search-icon-animate 1s ease-in-out infinite;
}

.fa-search {
  font-size: 16px;
}

.field-underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  background-image: linear-gradient(rgb(12, 94, 12), rgb(214, 214, 10));
  height: 3px;
  transition: width .5s ease;
  box-shadow: 0;
}

.high-price:focus+.field-underline {
  width: 100%;
  box-shadow: 0 0 5px 1px greenyellow;
}

.low-price:focus+.field-underline {
  width: 156px;
  box-shadow: 0 0 5px 1px greenyellow;
}

span {
  position: relative;
}

.low-price-label {
  position: relative;
  width: 200px;
  opacity: 0.8;
  width: 160px;
}

.high-price-label {
  position: relative;
  opacity: 0.8;
  width: 153px;
}

.checkbox-field {
  display: grid;
  grid-template-rows: repeat(4, 30px);
  grid-template-columns: 50px 20px;
  align-items: center;
}

.brand-filter-header {
  margin: 0;
  text-align: left;
  width: 200px;
}

.checkbox-brand-filter {
  grid-column: 1/2;
  margin: 0;
}

.checkbox-set {
  position: relative;
}

.brand-type {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: 1px solid chartreuse;
  outline: none !important;
  grid-column: 2/3;
  transition: all .4s ease;
  -webkit-transition: all .4s ease;
  -moz-transition: all .4s ease;
  -ms-transition: all .4s ease;
  -o-transition: all .4s ease;
}

.brand-type:checked {
  background-color: chartreuse;
  border: 2px solid whitesmoke;
}

.brand-type:checked+.ok-sign {
  color: black;
}

.brand-type:hover {
  transform: scale(1.3);
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
}

.ok-sign {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transition: color .5s ease .3s;
  -webkit-transition: color .5s ease .3s;
  -moz-transition: color .5s ease .3s;
  -ms-transition: color .5s ease .3s;
  -o-transition: color .5s ease .3s;
}

footer p {
  display: flex;
  font-weight: bold;
  height: 50px;
  align-items: flex-end;
  justify-content: center;
  border-top: solid 2px greenyellow;
}

/*contact page */

.describe-contact {
  margin: 10%;
  margin-top: 20px;
  text-align: center;
}

.contact {
  border-top: 2px solid greenyellow;
  display: flex;
  padding: 30px;
  font-size: 18px;
  display: flex;
  justify-content: space-around;
}

.contact-header {
  margin-top: 20px;
}

.brand-image {
  width: 250px;
  height: 250px;
  position: relative;
  left: 10%;
}

figure {
  margin: auto auto;
  width: 280px;
}

figcaption {
  position: relative;
  left: 10%;
}

ol {
  position: relative;
}

.contact li {
  margin-left: 30px;
  list-style: none;
}

.contact li i {
  margin-right: 10px;
  font-size: 28px;
}

.contact-details {
  margin-left: 30px;
  font-weight: bold;
  text-decoration: greenyellow underline;
  color: orangered;
}

h3 {
  text-align: center;
}

/*about page*/
.about-section {
  background-image: linear-gradient(45deg, rgb(37, 35, 35), rgb(10, 10, 10), rgb(0, 0, 0));
  background: cover;
  background-attachment: fixed;
  color: white;
  font-size: 17px;
}

.about-header {
  margin: 5%;
  margin-bottom: 3%;
  font-size: 48px;
}

.about-describe {
  text-align: justify;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 15px;
}

@keyframes search-icon-animate {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.4);
  }

  75% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.2);
  }
}

/*responsive*/
@media only screen and (max-width: 1520px) {
  .cart-conteiner {
    left: 200px;
  }
}

@media only screen and (max-width: 1350px) {
  .cart-conteiner {
    left: 150px;
  }

  .contact {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    row-gap: 10px;
  }
}

@media only screen and (max-width: 1090px) {
  .cart-conteiner {
    left: 50px;
  }

  .products-conteiner {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media only screen and (max-width: 820px) {
  .logo {
    width: 100px;
  }

  .cart-conteiner {
    left: 25px;
  }

  .products-conteiner {
    grid-template-columns: 1fr 1fr;
  }

  .search-text {
    position: relative;
    top: 50px;
    left: -30%;
  }
}

@media only screen and (max-width: 680px) {
  .products-conteiner {
    grid-template-columns: 1fr;
  }

  .product {
    width: 350px;
    position: relative;
    margin: auto auto;
    height: 400px;
  }

  .products-image {
    width: 100%;
    height: 150px;
  }

  .item {
    width: 100px;
    font-size: 16px;
    margin-left: 5px
  }
}

@media only screen and (max-width: 560px) {
  .logo {
    position: relative;
    left: -10px;
    transform: scale(1);
  }

  .nav-bar {
    position: relative;
    left: -15px;
  }

  .item {
    width: 90px;
    font-size: 18px;
    height: 40px;
    padding: 8px;
  }

  .cart-conteiner {
    left: 10px;
  }

  .sort {
    left: 5%;
  }

  .price-filtr {
    position: relative;
    left: 5%;
  }

  .high-price-label {
    margin-left: 60px;
  }

  .contact {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    row-gap: 10px;
  }

  .contact-list {
    display: grid;
  }
}

@media only screen and (max-width: 400px) {
  .logo {
    transform: scale(0.8);
    left: -5px;
    height: 80px;
  }

  .cart-conteiner {
    left: 3px;
  }

  .fa-shopping-cart {
    font-size: 22px;
  }

  .cart-info {
    left: -170px;
  }

  .cart-info::before {
    left: 86%;
  }

  .item {
    width: 80px;
    font-size: 16px;
    height: 40px;
    padding: 5px;
  }
}